import {Component, OnInit, ViewChild} from '@angular/core';
import {FidjStorageNode, ProfileService} from '../shared';
import {Router} from '@angular/router';
import {RadarNode} from 'raain-model';
import {IonModal} from '@ionic/angular';
import {environment} from '../../environments/environment';

@Component({
    selector: 'app-radars',
    templateUrl: 'radars.page.html',
    styleUrls: ['radars.page.scss'],
})
export class RadarsPage implements OnInit {
    @ViewChild(IonModal) modal: IonModal;

    public radarsInfoAlreadyViewed: any = {};
    public sessionData: FidjStorageNode;
    public radars: RadarNode[] = [];

    public appVersion = environment.version;

    constructor(
        private profile: ProfileService,
        private router: Router
    ) {}

    async ngOnInit() {
        await this.refresh();
    }

    async refresh(event?: any) {
        try {
            this.sessionData = await this.profile.refreshProfile();
            if (this.sessionData.infos.radarsInfoAlreadyViewed) {
                this.radarsInfoAlreadyViewed = this.sessionData.infos.radarsInfoAlreadyViewed;
            }
            this.radars = await this.profile.getRadars(this.profile.asTeamId);
        } catch (e) {
            console.error('refresh  pb:', e);
        }

        if (event) {
            event.target.complete();
        }

        if (!this.profile.isLoggedIn()) {
            return this.profile.gotoLout();
        }
    }

    async closeInfo(windowName: string) {
        this.radarsInfoAlreadyViewed[windowName] = true;
        if (!this.sessionData || !this.sessionData.infos) {
            return;
        }

        this.sessionData.infos.radarsInfoAlreadyViewed = this.radarsInfoAlreadyViewed;
        await this.profile.storeAll();
    }

    async saveRadar(radarNode: RadarNode) {
        await this.profile.putRadar(radarNode);
        await this.refresh();
        await this.modal.dismiss();
    }
}
